html,
body {
  margin: 0;
  padding: 0;
}

body {
  background-color: #282c34;
  font-family: "Arial", sans-serif;
  margin: 0; /* 기본 margin 제거 */
}

.App {
  text-align: center;
  font-size: 14px;
  height: 100%;
  min-height: 100%; /* .App 요소의 부모 요소에도 높이 설정이 필요할 수 있습니다. */
}

.container {
  height: 95vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 1.5vmin);
  color: white;
  padding: 0 5%;
}

.blue-team {
  background-color: rgb(110, 110, 255);
  padding: 5px 10px;
}

.red-team {
  background-color: rgb(255, 110, 110);
  padding: 5px 10px;
}

table {
  border-collapse: collapse;
  margin: 20px 0;
  width: 210px;
  table-layout: fixed;
  border-radius: 5px;
  overflow: hidden;
}

th,
td {
  border: 1px solid #ddd;
  padding: 4px 8px;
  font-size: calc(7px + 1vmin);
  text-align: left;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-radius: 5px;
}

th {
  background-color: #4caf50;
  color: white;
}

tr:nth-child(even) {
  background-color: #313131;
}

tr:hover {
  background-color: #ddd;
  cursor: pointer;
}

img {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  margin-right: 10px;
}

button {
  margin: 0 10px;
  padding: 10px 20px;
  font-size: calc(8px + 1.5vmin);
  text-align: center;
  border: none;
  border-radius: 5px;
  background-color: #4caf50;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

button span {
  display: block;
  line-height: 1.2;
}

button:hover {
  background-color: #45a049;
}

.modal-content {
  padding: 20px;
  max-height: 75vh; /* 조금 줄입니다. */
  overflow-y: auto; /* 세로 스크롤을 허용합니다. */
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start; /* 중앙이 아니라 상단에 배치될 수 있게 변경 */
  width: 100%; /* 이 줄 추가 */
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; /* 이 줄 추가 */
  padding: 20px; /* 이 줄 추가 */
}

.modal-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  width: 70%;
  max-width: 800px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.champion-item {
  flex-basis: calc(
    10% - 10px
  ); /* 가로에 10개의 챔피언을 배치하기 위한 너비 지정 */
  margin: 5px; /* 각 챔피언 항목 사이의 간격 */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer; /* 선택 가능함을 나타내기 위한 포인터 스타일 */
}

.champion-item img {
  width: 40px; /* 이미지 크기 조절 */
  height: 40px;
  border-radius: 50%;
}

.champion-item span {
  font-size: 12px; /* 폰트 크기 조절 */
  margin-top: 5px; /* 이미지와 이름 사이의 간격 조절 */
}

.ban-button {
  position: absolute;
  top: 10px;
  left: 10px;
}

.close-button {
  margin-top: 10px;
  background: #ff4d4d;
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  align-self: center; /* 이 줄 추가 */
}

.close-button:hover {
  background: #ff1a1a;
}

.banned {
  filter: grayscale(100%); /* 이미지를 회색으로 만듭니다. */
  color: #a9a9a9; /* 텍스트 색상을 회색으로 변경합니다. */
}

.champion-item.banned {
  background-color: #d3d3d3; /* 배경색을 회색으로 설정합니다. */
  cursor: not-allowed; /* 밴된 챔피언은 선택할 수 없음을 나타냅니다. */
}

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #333; /* 원하는 배경색으로 변경하세요 */
  color: #fff; /* 텍스트 색상을 설정하세요 */
  padding: 20px; /* 원하는 내부 여백으로 설정하세요 */
  text-align: center; /* 텍스트 중앙 정렬 */
}
